@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Marck+Script&display=swap);
body{font-size:62.5%;font-family:'Nunito', sans-serif;background-color:#f4f4f4;margin:0;padding:0;box-sizing:border-box;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;scroll-behavior:smooth}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

header{position:-webkit-sticky;position:sticky;top:0;width:100vw;height:8vh;border-bottom:solid 1px #333333;display:flex;align-items:center}header h1{margin-left:auto;position:-webkit-sticky;position:sticky;top:0;font-family:'Marck Script', cursive;font-size:3vmax;color:#333333;white-space:nowrap}header .cart{margin-left:auto;margin-right:1rem;font-size:2rem;text-decoration:none;color:#333333}header .active{color:#c09c39}

nav{width:100vw;height:8vh;position:-webkit-sticky;position:sticky;top:8vh;right:0;display:flex;justify-content:flex-end}nav .nav-wrapper{width:50rem;display:flex;justify-content:space-around;align-items:center;border-bottom:solid 1px #333333}nav .nav-wrapper .navlink{font-size:2vmax;text-decoration:none;color:#333333}nav .nav-wrapper .active{color:#c09c39}

.home{color:#333333;margin-top:5rem;margin-left:calc(min(8rem, 10vw));margin-right:calc(min(8rem, 10vw))}.home h2{font-size:2rem}.home p{font-size:1.5rem;max-width:60ch}

.about{color:#333333;margin-top:5rem;margin-left:calc(min(8rem, 10vw));margin-right:calc(min(8rem, 10vw))}.about h2{font-size:2rem}.about p{font-size:1.5rem;max-width:60ch}

.product{height:20rem;width:15rem;background-color:#cccccc;border:solid 1px #333333;border-radius:0.5rem;display:flex;flex-direction:column;align-items:center;justify-content:space-around;margin:4rem}.product .img-wrapper{height:10rem;width:10rem;border:solid 1px #333333;border-radius:0.5rem;overflow:hidden;display:grid;place-content:center}.product .img-wrapper img{width:15rem;height:15rem;object-fit:cover}.product h3{font-size:2rem;margin:0;color:#333333;font-weight:300}.product h3::first-letter{text-transform:uppercase}.product input{width:4rem;height:2rem;text-align:center;font-size:1.2rem}.product form{display:flex;flex-direction:column;align-items:center;grid-gap:5px;gap:5px}.product form button{height:2rem;font-size:1.2rem;cursor:pointer}.product form .input-wrapper{display:flex;align-items:center;grid-gap:2px;gap:2px}.product form .input-wrapper button{width:1.5rem;height:1.5rem;display:grid;place-content:center}.product form .input-wrapper input::-webkit-outer-spin-button,.product form .input-wrapper input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.product form .input-wrapper input[type=number]{-moz-appearance:textfield}

.products-wrapper{width:100vw;display:flex;justify-content:space-around;flex-wrap:wrap}

.cart-product-wrapper{max-width:30rem;display:flex;justify-content:space-between;align-items:center;border-bottom:solid 1px #333333}.cart-product-wrapper h3{font-size:1.5rem;color:#333333}.cart-product-wrapper h3:first-letter{text-transform:uppercase}.cart-product-wrapper .input-wrapper{display:flex;align-items:center;grid-gap:3px;gap:3px}.cart-product-wrapper .input-wrapper input{height:1.5rem;width:2rem;font-size:1.5rem;text-align:center}.cart-product-wrapper .input-wrapper input::-webkit-outer-spin-button,.cart-product-wrapper .input-wrapper input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.cart-product-wrapper .input-wrapper button{width:1.5rem;height:1.5rem;display:grid;place-content:center;font-size:1.2rem;cursor:pointer}

.cart-wrapper{display:flex;flex-direction:column;margin-top:5rem;margin-left:calc(min(8rem, 10vw));margin-right:calc(min(8rem, 10vw))}.cart-wrapper .order{width:5rem;height:2rem;display:grid;place-content:center;font-size:1.2rem;cursor:pointer;margin-top:4rem}


